<template>
  <div class="fromBox">
    <el-form :label-position="labelPosition" label-width="100px" :model="fromData">
      <el-form-item label="用户商家名">
        <el-input v-model="fromData.companyName"></el-input>
      </el-form-item>
      <el-form-item label="登陆账号">
        <el-input v-model="fromData.account"></el-input>
      </el-form-item>
      <el-form-item label="登陆密码">
        <el-input v-model="fromData.password"></el-input>
      </el-form-item>
      <el-form-item label="">
        <div style="text-align: left">
          <el-button type="primary" round class="submit" v-on:click="submitForm('form')"> 确定 </el-button>
        </div>
      </el-form-item>

    </el-form>
    <!-- 表单-->
  </div>
</template>
<style scoped>
  .submit{ width: 130px;height: 34px;line-height: 0px;background-color: #0e72b9;margin-top:40px;}
  .submit:hover{background-color: #409eff }
.fromBox{width: 500px;border: 1px solid #e1e1e1;margin: auto;background-color: white;padding: 40px 20px 40px 20px; border-radius:8px; box-shadow:0 0 16px #e2e7ea;padding-bottom: 10px; }
</style>
<script>
  import UserImg from '../../../static/img/icon/userDeFilt.png'
  export default {
    name: 'my-addUserFrom',
    data () {
      return {
        labelPosition: 'left',
        user: {
          imgUlr: UserImg, // 账号图片
          name: '小明管理员'
        },
        fromData: {
          companyName: null, // 商家名
          account: null, // 账号
          password: null // 密码
        },
        rules: {
          companyName: [
            { required: true, message: '请输入用户商家名', trigger: 'blur' },
            { min: 3, max: 20, message: '长度在 3 到 20 个字符', trigger: 'blur' }
          ],
          account: [
            { required: true, message: '请输入登陆账号', trigger: 'blur' },
            { min: 3, max: 20, message: '长度在 3 到 20 个字符', trigger: 'blur' }
          ],
          password: [
            { required: true, message: '请输入密码', trigger: 'blur' },
            { min: 3, max: 20, message: '长度在 3 到 20 个字符', trigger: 'blur' }
          ]
        },
        left: 'left'
      }
    },
    methods: {
      tableRowClassName ({row, rowIndex}) {
        var x = rowIndex % 2
        if (x === 0) {
          return ''
        } else if (x === 1) {
          return 'success-row'
        }
        return ''
      },
      handleCurrentChange (val) {
        console.log(`当前页: ${val}`)
      },
      login () {
        this.$router.push({ name: "map" })
      },
      submitForm (formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            console.log('提交')
          } else {
            console.log('error submit!!')
            return false
          }
        })
      }
    },
    computed: {

    },
    mounted () {

    },
    components: {
    }
  }
</script>




